@media (max-width: 768px) {
  .page {
    gap: 0 !important;
  }
  .page .page-header {
    height: 48px;
    padding: 0 16px;
    background: #ffffff;
  }
  .page .page-title {
    font-size: 16px;
    flex: 1;
    color: var(--primary-color);
  }
  .page .page-title-extra {
    flex: none;
  }
  .page .page-inner {
    box-sizing: border-box;
    --content-height: calc(var(--page-inner-height) - 24px);
    padding: 12px;
  }
  .page .page-inner:after {
    content: "";
    display: block;
    height: var(--toolbar-height);
  }
  .page .page-inner.no-padding {
    --content-height: var(--page-inner-height);
    padding: 0;
  }
}
.page.is-mobile {
  gap: 0 !important;
}
.page.is-mobile .page-header {
  height: 48px;
  padding: 0 16px;
  background: #ffffff;
}
.page.is-mobile .page-title {
  font-size: 16px;
  flex: 1;
  color: var(--primary-color);
}
.page.is-mobile .page-title-extra {
  flex: none;
}
.page.is-mobile .page-inner {
  box-sizing: border-box;
  --content-height: calc(var(--page-inner-height) - 24px);
  padding: 12px;
}
.page.is-mobile .page-inner:after {
  content: "";
  display: block;
  height: var(--toolbar-height);
}
.page.is-mobile .page-inner.no-padding {
  --content-height: var(--page-inner-height);
  padding: 0;
}

.page-title-outer {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.page-title {
  font-size: 20px;
  font-weight: 500;
}

.page-inner {
  --content-height: var(--page-inner-height);
  min-height: calc(var(--page-inner-height) + var(--toolbar-height));
  display: flex;
  flex-direction: column;
  flex: 1;
}

.page-title-extra {
  display: flex;
  justify-content: flex-end;
  flex: 1;
  max-width: 50%;
}

.page-loading {
  width: 100%;
  background: #fff;
  padding: 12px;
  border-radius: 12px;
}

.back-icon {
  display: block;
  transform: rotate(180deg);
}